<template>
<div>
    <el-form :model="objFormAnno" :rules="rules" ref="objFormAnno" label-width="130px">
        <el-form-item label="公告类型" prop="type">
            <el-select v-model="objFormAnno.type" placeholder="请选择公告类型">
                <el-option v-for="(item,idx) in anno_type" :label="item.label" :value="item.value" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="渠道" prop="channels">
            <el-checkbox-group v-model="objFormAnno.channels">
                <el-checkbox v-for="(item,idx) in aChannels" :label="item.id" :key="idx">{{item.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="优先级" prop="sort">
            <el-select v-model="objFormAnno.sort" placeholder="请选择优先级">
                <el-option v-for="(item,idx) in aLevels" :label="item.label" :value="item.value" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="短标题" prop="shortTitle">
            <el-input v-model="objFormAnno.shortTitle" placeholder="请输入短标题"></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="title">
            <el-input v-model="objFormAnno.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="公告内容" prop="content">
            <el-input type="textarea" v-model="objFormAnno.content" placeholder="请输入公告内容"></el-input>
        </el-form-item>
        <el-form-item label="开始时间" prop="start">
            <el-date-picker v-model="objFormAnno.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="end">
            <el-date-picker v-model="objFormAnno.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="游戏内公告类型" prop="gameType">
            <el-radio-group v-model="objFormAnno.gameType" prop="receiverType">
                <el-radio v-for="(item,idx) in aGameTypes" :label="item.type" :key="idx">{{item.name}}</el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item label="间隔时间" prop="intervalTime" v-if="objFormAnno.type=='3'">
            <el-input-number v-model="objFormAnno.intervalTime" controls-position="right" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="公告底图">
            <el-input v-model="objFormAnno.imageName" placeholder="请输入公告底图"></el-input>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="submitForm('objFormAnno')">发布公告</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
export default {
    name: "anno-create",
    data() {
        return {
            objFormAnno: {
                type: "",
                channels: [],
                sort: "",
                shortTitle: "",
                title: "",
                content: "",
                start: null,
                end: null,
                aGameTypes: "",
                intervalTime: 0,
                imageName: ''
            },
            rules: {
                type: [{
                    required: true,
                    message: "请选择公告类型",
                    trigger: "change"
                }],
                channels: [{
                    type: "array",
                    required: true,
                    message: "请至少选择一个渠道",
                    trigger: "change"
                }],
                sort: [{
                    required: true,
                    message: "请选择优先级",
                    trigger: "change"
                }],
                shortTitle: [{
                    required: true,
                    message: "请输入短标题",
                    trigger: "blur"
                }],
                title: [{
                    required: true,
                    message: "请输入标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入公告内容",
                    trigger: "blur"
                }],
                start: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                end: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                gameType: [{
                    required: true,
                    message: "请选择游戏内公告类型",
                    trigger: "change"
                }],
                intervalTime: [{
                    required: true,
                    message: "请选择公告类型"
                }]
            },

            // 公告类型
            anno_type: [{
                    label: "登录页公告",
                    value: "1"
                },
                {
                    label: "游戏内公告",
                    value: "2"
                },
                {
                    label: "跑马灯",
                    value: "3"
                }
            ],
            // 渠道
            aChannels: [],
            // 优先级
            aLevels: [{
                    label: "high",
                    value: "1"
                },
                {
                    label: "mid",
                    value: "2"
                },
                {
                    label: "low",
                    value: "3"
                }
            ],
            // 游戏内公告类型
            aGameTypes: []
        };
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormAnno));
                    this.$fetch.post(API.anno.send_notice, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.$emit("changeTab", "todo");
                            this.$refs[formName].resetFields();
                            this.$message.success(res.desc);
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        fetchChannels() {
            this.$fetch.post(API.anno.channels).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aChannels = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchGameTypes() {
            this.$fetch.post(API.anno.game_notice_type_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aGameTypes = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchChannels();
        this.fetchGameTypes();
    }
};
</script>
