<template>
<div>

    <el-table ref="multipleTable" :data="aTableDataHistory" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="id" label="ID" min-width="70"></el-table-column>
        <el-table-column label="渠道" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnChannel(scope.row.channels)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="start" label="开始时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="end" label="结束时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="shortTitle" label="短标题" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="title" label="标题" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="content" label="公告内容" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column label="标签" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnType(scope.row.type)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="sort" label="优先级" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnSort(scope.row.sort)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="imageName" label="公告底图" min-width="100" show-overflow-tooltip></el-table-column>
    </el-table>
</div>
</template>

<script>
export default {
    name: "anno-history",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            // 公告类型
            anno_type: [{
                    label: "登录页公告",
                    value: "1"
                },
                {
                    label: "游戏内公告",
                    value: "2"
                },
                {
                    label: "跑马灯",
                    value: "3"
                }
            ],
            // 优先级
            aLevels: [{
                    label: "high",
                    value: "1"
                },
                {
                    label: "mid",
                    value: "2"
                },
                {
                    label: "low",
                    value: "3"
                }
            ],
            aTableDataHistory: [],
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "history") {
                    this.fetchHistoryList();
                }
            }
        }
    },
    methods: {

        returnChannel(params) {
            let arr = [];
            params.forEach(item => {
                arr.push(item.name)
            })
            let show = arr.join(',')
            return show;
        },
        returnType(key) {
            let res = "--";
            this.anno_type.forEach(item => {
                if (item.value + "" == key + "") {
                    res = item.label;
                }
            });
            return res;
        },
        returnSort(key) {
            let res = "--";
            this.aLevels.forEach(item => {
                if (item.value + "" == key + "") {
                    res = item.label;
                }
            });
            return res;
        },
        fetchHistoryList() {
            this.$fetch.post(API.anno.over_time_notice).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableDataHistory = res.data;
                } else {
                    this.$message.error();
                }
            });
        },
    },
    mounted() {
        this.fetchHistoryList()
    }
};
</script>
