<template>
<div>
    <el-button :disabled="aSelectTodo.length<1" @click="handleMultTodo()">全部删除</el-button>
    <el-table ref="multipleTable" :data="aTableDataTodo" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectRowsTodo">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="id" label="ID" min-width="70"></el-table-column>
        <el-table-column label="渠道" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnChannel(scope.row.channels)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="start" label="开始时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="end" label="结束时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="shortTitle" label="短标题" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="title" label="标题" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="content" label="公告内容" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column label="标签" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnType(scope.row.type)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="sort" label="优先级" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{returnSort(scope.row.sort)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="imageName" label="公告底图" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column label="操作" min-width="100">
            <template slot-scope="scope">
                <el-button @click.native.prevent="handleClickEditTodo(scope.row)" type="text" size="small">编辑</el-button>
                <el-button @click.native.prevent="handleClickDelete(scope.row)" type="text" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog title="修改公告" :visible.sync="bDialogVisible">
        <el-form :model="objFormAnnoEdit" :rules="rules" ref="objFormAnnoEdit" label-width="130px">
            <el-form-item label="公告类型" prop="type">
                <el-select v-model="objFormAnnoEdit.type" placeholder="请选择公告类型">
                    <el-option v-for="(item,idx) in anno_type" :label="item.label" :value="item.value" :key="idx"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="渠道" prop="channels">
                <el-checkbox-group v-model="objFormAnnoEdit.channels">
                    <el-checkbox v-for="(item,idx) in aChannels" :label="item.id" :key="idx">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="优先级" prop="sort">
                <el-select v-model="objFormAnnoEdit.sort" placeholder="请选择优先级">
                    <el-option v-for="(item,idx) in aLevels" :label="item.label" :value="item.value" :key="idx"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="短标题" prop="shortTitle">
                <el-input v-model="objFormAnnoEdit.shortTitle" placeholder="请输入短标题"></el-input>
            </el-form-item>
            <el-form-item label="标题" prop="title">
                <el-input v-model="objFormAnnoEdit.title" placeholder="请输入标题"></el-input>
            </el-form-item>
            <el-form-item label="公告内容" prop="content">
                <el-input type="textarea" v-model="objFormAnnoEdit.content" placeholder="请输入公告内容"></el-input>
            </el-form-item>
            <el-form-item label="开始时间" prop="start">
                <el-date-picker v-model="objFormAnnoEdit.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始时间"></el-date-picker>
            </el-form-item>
            <el-form-item label="结束时间" prop="end">
                <el-date-picker v-model="objFormAnnoEdit.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束时间"></el-date-picker>
            </el-form-item>

            <el-form-item label="游戏内公告类型" prop="gameType">
                <el-radio-group v-model="objFormAnnoEdit.gameType" prop="receiverType">
                    <el-radio v-for="(item,idx) in aGameTypes" :label="item.type" :key="idx">{{item.name}}</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="间隔时间" prop="intervalTime" v-if="objFormAnnoEdit.type=='3'">
                <el-input-number v-model="objFormAnnoEdit.intervalTime" controls-position="right" :min="1"></el-input-number>
            </el-form-item>
            <el-form-item label="公告底图">
                <el-input v-model="objFormAnnoEdit.imageName" placeholder="请输入公告底图"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleClickUpdateAnno('objFormAnnoEdit')">修改公告</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>

<script>
export default {
    name: "anno-todo",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            // 公告类型
            anno_type: [{
                    label: "登录页公告",
                    value: "1"
                },
                {
                    label: "游戏内公告",
                    value: "2"
                },
                {
                    label: "跑马灯",
                    value: "3"
                }
            ],
            // 渠道
            aChannels: [],
            // 优先级
            aLevels: [{
                    label: "high",
                    value: "1"
                },
                {
                    label: "mid",
                    value: "2"
                },
                {
                    label: "low",
                    value: "3"
                }
            ],
            // 游戏内公告类型
            aGameTypes: [],
            // 公告修改模态框
            bDialogVisible: false,

            objFormAnnoEdit: {
                intervalTime: 0
            },
            rules: {
                type: [{
                    required: true,
                    message: "请选择公告类型",
                    trigger: "change"
                }],
                channels: [{
                    type: "array",
                    required: true,
                    message: "请至少选择一个渠道",
                    trigger: "change"
                }],
                sort: [{
                    required: true,
                    message: "请选择优先级",
                    trigger: "change"
                }],
                shortTitle: [{
                    required: true,
                    message: "请输入短标题",
                    trigger: "blur"
                }],
                title: [{
                    required: true,
                    message: "请输入标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入公告内容",
                    trigger: "blur"
                }],
                start: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                end: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                gameType: [{
                    required: true,
                    message: "请选择公告类型",
                    trigger: "change"
                }],
                intervalTime: [{
                    required: true,
                    message: "请选择公告类型"
                }]
            },
            aTableDataTodo: [],
            aSelectTodo: []
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "done") {
                    this.fetchDoneList();
                }
            }
        }
    },
    methods: {
        returnChannel(params) {
            let arr = [];
            params.forEach(item => {
                arr.push(item.name);
            });
            let show = arr.join(",");
            return show;
        },
        returnType(key) {
            let res = "--";
            this.anno_type.forEach(item => {
                if (item.value + "" == key + "") {
                    res = item.label;
                }
            });
            return res;
        },
        returnSort(key) {
            let res = "--";
            this.aLevels.forEach(item => {
                if (item.value + "" == key + "") {
                    res = item.label;
                }
            });
            return res;
        },
        handleClickEditTodo(row) {
            this.objFormAnnoEdit = JSON.parse(JSON.stringify(row));
            let arr = [];
            row.channels.forEach(item => {
                arr.push(item.id);
            });
            this.objFormAnnoEdit.sort += "";
            this.objFormAnnoEdit.type += "";
            // this.objFormAnnoEdit.gameType += "";
            this.objFormAnnoEdit.channels = arr;
            this.bDialogVisible = true;
        },
        handleSelectRowsTodo(val) {
            this.aSelectTodo = val;
        },
        handleClickDelete(row) {
            this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let arr = this.aSel;
                let params = {
                    id: [row.id]
                };
                this.$fetch.post(API.anno.delete_notice, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchDoneList();
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },
        handleClickUpdateAnno(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormAnnoEdit));
                    this.$fetch.post(API.anno.update_notice, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.fetchDoneList();
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
            this.bDialogVisible = false;
        },
        handleMultTodo() {
            this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let arr = this.aSelectTodo.map(item => {
                    return item.id;
                });
                let params = {
                    id: arr
                };

                this.$fetch.post(API.anno.delete_notice, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchDoneList();
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },

        fetchDoneList() {
            this.$fetch.post(API.anno.in_release_notice).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableDataTodo = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchChannels() {
            this.$fetch.post(API.anno.channels).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aChannels = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchGameTypes() {
            this.$fetch.post(API.anno.game_notice_type_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aGameTypes = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchDoneList();
        this.fetchChannels();
        this.fetchGameTypes();
    }
};
</script>
